* {
  margin: 0;
  padding: 0;
  color: #00eeff;

  --cyan: #00eeff;
  --blue: #005BF7;
  --green: rgb(0, 255, 0);
  --orange: rgb(249, 107, 25);
  --yellow: #F7A800;
  --magenta: #b700ff;
}

html {
  font-size: 10px;
}

body {
  background: #000919;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
header {
    width: 100%;
  
    display: flex;
    justify-content: space-between;
    align-items: center;
  
    margin-bottom: 1.5rem;
  }
  
  .headerText {
    margin: 1rem;
  }
  
  .wkndTitle {
    font-family: 'bebas-neue', sans-serif;
    font-size: 4rem;
    word-spacing: 1px;
    color: white;
  }
  
  .dates {
    color: var(--cyan);
    font-size: 2rem;
    font-family: 'Raleway', sans-serif;
  }
  
  .profileImg {
    margin: 1rem 2rem;
    width: 6rem;
    height: 6rem;
    background-color: var(--cyan);
    border-radius: 100%;
    -webkit-animation: picExpand .5s cubic-bezier(0.075, 0.82, 0.165, 1) .9s backwards;
            animation: picExpand .5s cubic-bezier(0.075, 0.82, 0.165, 1) .9s backwards;
  }
.movie {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    margin: 2rem;

    -webkit-transition: all 1s ease-in;

    transition: all 1s ease-in;
}

.optionInput {
    margin-bottom: 2rem;
    border: none;
    background: none;
    border-bottom: .5px solid var(--cyan);

    text-align: center;
    font-size: 2.5rem;
    color: var(--cyan);
}

.optionButton {
    color: var(--orange);
    margin-bottom: 1rem;
}

.movieItem {
    margin: 1rem;
    text-align: center;
    font-family: 'bebas neue', sans-serif;
    font-size: 3rem;
    text-shadow: 0 0 5px var(--cyan);
    -webkit-transition: all 1s ease-in;
    transition: all 1s ease-in;
}

.card {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  
    width: 95vw;
    margin: 0 1rem 1rem 1rem;
  
    border-radius: 10px;
    background: rgb(12, 22, 38);
    -webkit-transition: all 1s ease-in;
    transition: all 1s ease-in;
    -webkit-animation: cardShuffleIn 1s cubic-bezier(0.075, 0.82, 0.165, 1) both;
            animation: cardShuffleIn 1s cubic-bezier(0.075, 0.82, 0.165, 1) both;
  }

  .card.expanded {
    height: 100%;
    -webkit-transform: scaleY(-5);
            transform: scaleY(-5);
    -webkit-transition: all 1s ease-in;
    transition: all 1s ease-in;
  }

  .rejected {
    -webkit-filter: opacity(.25);
            filter: opacity(.25);
    border: 1px solid red;
  }
  
  .rejected .reject {
    opacity: 0;
  }
  
  .accepted {
    border: 1px solid rgb(0, 255, 0);
  }
  
  .accepted .accept {
    opacity: 0;
  }
  
  #SAT {
    -webkit-animation-delay: .2s;
            animation-delay: .2s;
  }
  
  #SUN {
    -webkit-animation-delay: .4s;
            animation-delay: .4s;
  }
  
  .cardHeader {
    display: flex;
    justify-content: space-between;
    align-content: flex-start;
    padding: 0.5rem 1rem;
  }
  
  .day {
    font-family: 'bebas-neue', sans-serif;
    color: var(--cyan);
    font-size: 5rem;
    letter-spacing: 3pt;
  }
  
  .ellipses {
    width: .7rem;
    height: .7rem;
    align-self: center;
    margin-right: 10%;
    background-color: var(--cyan);
    border: none;
    border-radius: 1rem;
    -webkit-transform-origin: center;
            transform-origin: center;
  }
  
  .ellipses::before, .ellipses::after {
    content: '';
    position: relative;
    display: inline-block;
    width: inherit;
    height: inherit;
    background-color: var(--cyan);
    border-radius: inherit;
    -webkit-transform-origin: center;
            transform-origin: center;
  }
  
  .ellipses::before {
    right: 2rem;
    top: -1.3rem;
  }
  
  .ellipses::after {
    left: 2rem;
    top: -3.7rem;
  }
  
  .ellipses.open {
    background-color: var(--navy);
  }
  
  .ellipses.open::before {
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-animation: menuTransformBefore .3s ease-in forwards;
            animation: menuTransformBefore .3s ease-in forwards;
  }
  
  .ellipses.open::after {
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-animation: menuTransformAfter .3s ease-in forwards;
            animation: menuTransformAfter .3s ease-in forwards;
  
  }
  
  .activities {
    display: flex;
    justify-content: space-around;
    margin: .5rem 2rem;
  }
  
  svg, path {
    pointer-events: none;
  }

  .icon {
    width: 15vw;
    height: 15vw;
    background-color: transparent;
    -webkit-transition: all .2s ease-out;
    transition: all .2s ease-out;
  }

  .icon:active {
    -webkit-transform: scale(.95);
            transform: scale(.95)
  }
  
  #food path{
    fill: var(--yellow);
  }
  
  #food.selected {
    -webkit-filter: drop-shadow(0px 0px 20px var(--yellow));
    filter: drop-shadow(0px 0px 20px var(--yellow));
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  
  #dnd path{
    fill: var(--blue);
  }
  
  #dnd.selected {
    -webkit-filter: drop-shadow(0px 0px 20px var(--blue));
    filter: drop-shadow(0px 0px 20px var(--blue));
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  
  #movie path {
    fill: var(--orange);
  }
  
  #movie.selected {
    -webkit-filter: drop-shadow(0px 0px 20px var(--orange));
    filter: drop-shadow(0px 0px 20px var(--orange));
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
  
  .cardFooter {
    display: flex;
    justify-content: space-between;
    justify-self: flex-end;
    margin: .5rem 2rem;
    font-family: 'Bebas Neue', sans-serif;
  }
  
  .cardFooter button:first-child {
    color: hsl(0, 100%, 50%);
  }
  
  .cardFooter button:last-child {
    color: rgb(0, 255, 0);
  }
  
  button {
    border: none;
    background-color: inherit;
    font-size: 2rem;
  }
