.movie {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    
    margin: 2rem;

    transition: all 1s ease-in;
}

.optionInput {
    margin-bottom: 2rem;
    border: none;
    background: none;
    border-bottom: .5px solid var(--cyan);

    text-align: center;
    font-size: 2.5rem;
    color: var(--cyan);
}

.optionButton {
    color: var(--orange);
    margin-bottom: 1rem;
}

.movieItem {
    margin: 1rem;
    text-align: center;
    font-family: 'bebas neue', sans-serif;
    font-size: 3rem;
    text-shadow: 0 0 5px var(--cyan);
    transition: all 1s ease-in;
}
