.card {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  
    width: 95vw;
    margin: 0 1rem 1rem 1rem;
  
    border-radius: 10px;
    background: rgb(12, 22, 38);
    transition: all 1s ease-in;
    animation: cardShuffleIn 1s cubic-bezier(0.075, 0.82, 0.165, 1) both;
  }

  .card.expanded {
    height: 100%;
    transform: scaleY(-5);
    transition: all 1s ease-in;
  }

  .rejected {
    filter: opacity(.25);
    border: 1px solid red;
  }
  
  .rejected .reject {
    opacity: 0;
  }
  
  .accepted {
    border: 1px solid rgb(0, 255, 0);
  }
  
  .accepted .accept {
    opacity: 0;
  }
  
  #SAT {
    animation-delay: .2s;
  }
  
  #SUN {
    animation-delay: .4s;
  }
  
  .cardHeader {
    display: flex;
    justify-content: space-between;
    align-content: flex-start;
    padding: 0.5rem 1rem;
  }
  
  .day {
    font-family: 'bebas-neue', sans-serif;
    color: var(--cyan);
    font-size: 5rem;
    letter-spacing: 3pt;
  }
  
  .ellipses {
    width: .7rem;
    height: .7rem;
    align-self: center;
    margin-right: 10%;
    background-color: var(--cyan);
    border: none;
    border-radius: 1rem;
    transform-origin: center;
  }
  
  .ellipses::before, .ellipses::after {
    content: '';
    position: relative;
    display: inline-block;
    width: inherit;
    height: inherit;
    background-color: var(--cyan);
    border-radius: inherit;
    transform-origin: center;
  }
  
  .ellipses::before {
    right: 2rem;
    top: -1.3rem;
  }
  
  .ellipses::after {
    left: 2rem;
    top: -3.7rem;
  }
  
  .ellipses.open {
    background-color: var(--navy);
  }
  
  .ellipses.open::before {
    transform-origin: center;
    animation: menuTransformBefore .3s ease-in forwards;
  }
  
  .ellipses.open::after {
    transform-origin: center;
    animation: menuTransformAfter .3s ease-in forwards;
  
  }
  
  .activities {
    display: flex;
    justify-content: space-around;
    margin: .5rem 2rem;
  }
  
  svg, path {
    pointer-events: none;
  }

  .icon {
    width: 15vw;
    height: 15vw;
    background-color: transparent;
    transition: all .2s ease-out;
  }

  .icon:active {
    transform: scale(.95)
  }
  
  #food path{
    fill: var(--yellow);
  }
  
  #food.selected {
    -webkit-filter: drop-shadow(0px 0px 20px var(--yellow));
    filter: drop-shadow(0px 0px 20px var(--yellow));
    transform: scale(1.05);
  }
  
  #dnd path{
    fill: var(--blue);
  }
  
  #dnd.selected {
    -webkit-filter: drop-shadow(0px 0px 20px var(--blue));
    filter: drop-shadow(0px 0px 20px var(--blue));
    transform: scale(1.05);
  }
  
  #movie path {
    fill: var(--orange);
  }
  
  #movie.selected {
    -webkit-filter: drop-shadow(0px 0px 20px var(--orange));
    filter: drop-shadow(0px 0px 20px var(--orange));
    transform: scale(1.05);
  }
  
  .cardFooter {
    display: flex;
    justify-content: space-between;
    justify-self: flex-end;
    margin: .5rem 2rem;
    font-family: 'Bebas Neue', sans-serif;
  }
  
  .cardFooter button:first-child {
    color: hsl(0, 100%, 50%);
  }
  
  .cardFooter button:last-child {
    color: rgb(0, 255, 0);
  }
  
  button {
    border: none;
    background-color: inherit;
    font-size: 2rem;
  }